import React from 'react'
import CssBaseline from "@material-ui/core/CssBaseline";
import Container from "@material-ui/core/Container";
import { makeStyles } from "@material-ui/core/styles";
import Box from '@material-ui/core/Box';
import Typography from "@material-ui/core/Typography"
import Avatar from "@material-ui/core/Avatar";
import Footer from "../../components/Footer";
import Header from "../../components/Header";

const useStyles = makeStyles((theme) => ({
    root: {
        marginTop: 5,
        width: "80%",
        [theme.breakpoints.down("xs")]: {
          width: "100% !important", // Overrides inline-style
        },
      },
      title:{
        fontSize:30,
        marginTop:20,
        marginBottom:20,
      },
      avatar: {
        width: theme.spacing(5),
        height: theme.spacing(5),
        // color: theme.palette.getContrastText(deepOrange[500]),
        // backgroundColor: deepOrange[500],
        marginRight: 10,
        
      },
      content:{
          marginTop:20,
          fontSize: 20,
          [theme.breakpoints.down("xs")]: {
            fontSize: 15,
          },
      },
      headerImage:{
        backgroundImage: 'url(https://cdn.pixabay.com/photo/2017/10/24/12/36/fog-2884435_1280.jpg)',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center center',
        backgroundSize:"100%",
        objectFit:"stretch",
        height:250,

        marginBottom:10,
      }
}));

const blogItem =
  {
    title:"Title of a longer featured blog post",
    body: "According to an "
    +"article from the New York Times, a portrait produced by Artificial Intelligence was"
    +"hanging at Christie’s New York opposite an Andy Warhol print and beside a bronze "
    +"work by Roy Lichtenstein. It was sold for well over double the price realized "
    +"by both those pieces combined. “Edmond de Belamy, from La Famille de Belamy” sold for" 
    +"$432,500 including fees, over 40 times Christie’s initial estimate of $7,000-$10,000. The buyer was an anonymous phone bidder."
    +"Artificial Intelligence (AI) is getting more importance in different fields and it’s"
    +" drastically changing the nature of creative processes. "
    +" Did you imagine using AI to create art? This is happening right now! TCheck this music video using AI",
    image:"data:image/png;base64,",
    publishedDate : "Nov 13",
    author:"Abdyrasul Oraznyyazov"
  
  }

export default function BlogItem() {
    const classes = useStyles();
    return (
        <React.Fragment>
            <CssBaseline />
            <Header name ="BLOG"></Header>
      <Container maxWidth="xl" className={classes.root}>
      <Box className={classes.headerImage}  >

</Box>  
       <Box display="flex" flexDirection="column">
        <Typography component="h2" variant="h5" className={classes.title}>
        {blogItem.title}
        </Typography>
        <Box display="flex" flexDirection="row">
        <Avatar className={classes.avatar} src ={blogItem.image}></Avatar>
        <Typography component = "h3" variant="h6">
       {blogItem.author}
        </Typography>
        <Box flexGrow={1}>
        <Typography variant="subtitle1" color="textSecondary"  style = {{textAlign:"right"}}>
        {blogItem.publishedDate}
        </Typography>
        </Box>
        
        </Box>
        <Typography component = "p" variant="body2" className = {classes.content}>
        {blogItem.body}
        </Typography>
      </Box> 
      {/* <ReactMarkdown source ={`# This is a header\n\nAnd this is a paragraph`}/> */}
      </Container>
      <Footer></Footer>
        </React.Fragment>
    )
}
